<script context="module">
  export function saveSearch(str) {
    let store = getFromLocal("lastSearch");
    if (!store) store = [];
    store = [...new Set([str, ...store])].slice(0, 7);
    setToLocal("lastSearch", store);
  }
</script>

<script>
  console.clear();
  import { sortObjetcByKey } from "../js/t&u";
  import { onMount } from "svelte";

  // Components imports
  import Header from "./Header.svelte";
  import ProductList from "./ProductList.svelte";
  import Footer from "./Footer.svelte";
  import Aside from "./Aside.svelte";
  import Icon from "./Icons.svelte";

  let QUERY,
    PRODUCTS,
    PRODUCTS_SHOWED,
    LAST_SEARCH,
    CATEGORIES,
    PRODUCTS_TYPES,
    BRANDS,
    ONLINE = false;

  window.setOfBrand = new Set();

  let showProductOnStock = false;
  let isOpenMenu = false;
  const limitOfResultToShow = 100;
  const urlOfData =
    "https://spreadsheets.google.com/feeds/list/1FjerBKgvNepZfQkPaUbd9DMy5-SMr-XxEKeNsZhcPM4/od6/public/values?alt=json";

  $: QUERY, searchOnList();

  onMount(async () => {
    try {
      QUERY = "";
      // FROM LOCAL
      if (getFromLocal("Query")) {
        QUERY = getFromLocal("Query").replace(/\\|#/gi, "");
      }
      // FROM URL
      if (location.hash.slice(1)) {
        QUERY = decodeURI(
          location.hash.replace(/(_)/g, " ").replace(/(\\|#)/gi, "")
        );
      }
      PRODUCTS = getFromLocal("Products");
      CATEGORIES = getFromLocal("Categories");
      PRODUCTS_TYPES = getFromLocal("ProductsTypes");
      BRANDS = getFromLocal("Brands");

      // FROM NETWORK
      PRODUCTS = await updateProducts();
      searchOnList();

      // TIMER
      const timer = setInterval(async () => {
        PRODUCTS = await updateProducts();
        searchOnList();
      }, 60 * 60000);
    } catch (error) {
      console.error(error);
    }
  });

  // ------------------------------------
  // Update list of product from source
  // ------------------------------------
  async function updateProducts() {
    try {
      const data = await fechingData(urlOfData);
      return data;
    } catch (error) {
      console.error(error);
    }
  }

  // ------------------------------------
  // Search $Query on list to be displayed
  // ------------------------------------
  function searchOnList(str = QUERY) {
    if (!PRODUCTS) return;
    let results;
    if (!!str) {
      // Search products by str
      str = str.trim();
      location.hash = str.replace(/( )/g, "_"); // Update location.hash
      setToLocal("Query", str);

      results = PRODUCTS.filter((item) => {
        try {
          const expession = new RegExp(escapeRegExp(str), "gi");
          console.info(`Serching: ${expession}`);
          return (
            item.name.match(expession) ||
            item.brand.match(expession) ||
            item.categorie.match(expession) ||
            item.productType.match(expession)
          );
        } catch (error) {
          console.error(error);
          return false;
        } // Try
      });
      results = sortObjetcByKey(results, "name"); // Order results
    }

    // Show shuffle default results
    else {
      results = PRODUCTS.sort(() => Math.random() - 0.5);
    }
    results = results.slice(0, limitOfResultToShow); // Cut results to limit
    PRODUCTS_SHOWED = results;
    LAST_SEARCH = getFromLocal("lastSearch");

    function escapeRegExp(text) {
      try {
        // return text.replace(/[-[\]{}()*+?.,\\^$|#\s]/g, "\\$&");
        return text.replace(/[-[\]{}()*+?.,\\^$|#]/g, "\\$&");
      } catch (error) {
        console.error(error);
        return "";
      }
    } // escapeRegExp()
  }

  // ------------------------------------
  // Get JSON from Google
  // ------------------------------------
  async function fechingData(url) {
    try {
      console.time("Fething data from database");
      const response = await fetch(url);
      const data = await response.json();
      console.timeEnd("Fething data from database");
      return parseData(data.feed.entry);
    } catch (error) {
      console.error(error);
    }
  }

  // ------------------------------------
  // Prepare and filter data
  // ------------------------------------
  function parseData(data) {
    //let categories = [];
    //let productsTypes = [];
    //let brands = [];
    const brands = new Set(),
      productsTypes = new Set(),
      categories = new Set();

    let results = data.reduce((accu, item, i) => {
      // Verifica Stock, Activo, Web
      if (
        item.gsx$act.$t !== "*" ||
        item.gsx$web.$t !== "*" ||
        item.gsx$stock.$t == ""
      )
        return accu;

      const id = i;
      let {
        gsx$producto: { $t: name },
        gsx$pvp: { $t: price },
        gsx$fin: { $t: feeAmount },
        gsx$cuotas: { $t: feeValue },
        gsx$stock: { $t: stock },
        gsx$d: { $t: ofert },
        gsx$categoria: { $t: categorie },
        gsx$rubro: { $t: productType },
        gsx$marca: { $t: brand },
        gsx$gtia: { $t: warranty },
        gsx$desc: { $t: description },
        gsx$img: { $t: image },
        gsx$act: { $t: active },
        gsx$web: { $t: activeForWeb },
        id: { $t: link },
        updated: { $t: updated },
      } = item;

      feeValue = feeValue.split(",");

      ofert = !!ofert;
      active = !!active;
      activeForWeb = !!activeForWeb;
      const toAdd = {
        id,
        active,
        activeForWeb,
        name,
        price,
        feeAmount,
        feeValue,
        stock,
        ofert,
        categorie,
        productType,
        brand,
        warranty,
        description,
        image,
        link,
        updated,
      };

      accu = [...accu, toAdd];
      // categories = [...categories, categorie.capitalize()];
      // productsTypes = [...productsTypes, productType.capitalize()];
      // brands = [...brands, brand.capitalize()];

      brands.add(brand.capitalize());
      productsTypes.add(productType.capitalize());
      categories.add(categorie.capitalize());

      return accu;
    }, []); // Reduce

    ONLINE = true;

    CATEGORIES = [...categories].sort();
    setToLocal("Categories", CATEGORIES);

    PRODUCTS_TYPES = [...productsTypes].sort();
    setToLocal("ProductsTypes", PRODUCTS_TYPES);

    BRANDS = [...brands].sort();
    setToLocal("Brands", BRANDS);

    results = [...sortObjetcByKey(results, "name")];
    setToLocal("Products", results);

    return results;
  }

  /**
   * Save last search
   */

  window.saveSearch = (str) => {
    let store = getFromLocal("lastSearch");
    if (!store) store = [];
    store = [...new Set([str, ...store])].slice(0, 7);
    setToLocal("lastSearch", store);
  };

  function setToLocal(key, value) {
    try {
      localStorage.setItem(key, JSON.stringify(value));
    } catch (error) {
      console.error(error);
    }
  }

  function getFromLocal(key) {
    try {
      return JSON.parse(localStorage.getItem(key));
    } catch (error) {
      console.error(error);
    }
  }

  /**
   * * Capitalize the first letter of each word of a given string
   * * Usage: 'capItalezE aLl fIrSt leTTers'.capitalize()
   */
  String.prototype.capitalize = function () {
    try {
      return this.split(" ")
        .filter((value) => value)
        .reduce(
          (text, word) =>
            text + " " + word[0].toUpperCase() + word.substr(1).toLowerCase(),
          ""
        ); // Reduce
    } catch (error) {
      console.info(`Error: '${this}'.capitalize()'`);
      return this;
    }
  };

  /**
   * * Copy text to clipboard
   * * Usage: copyToClipboard("Text to Copy");
   */
  window.copyToClipboard = (textToCopy) => {
    try {
      let el = document.createElement("textarea");
      el.value = textToCopy;
      document.body.appendChild(el);
      el.select();
      document.execCommand("copy");
      document.body.removeChild(el);
      console.info(`Text copied: ${textToCopy}`);
    } catch (error) {
      console.error(error);
    }
  };
</script>

<style>
  main {
    height: 100vh;
    color: var(--color-5);
    font-size: 1em;
  }
  .noProducts {
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .noProducts h2 {
    font-size: 1.5rem;
  }
</style>

<main>
  <Header />

  <!-- {#if (location.host = '0.0.0.0:8080')}
    <Icon iconName="close" title="Click" stroke="orangered" fill="orangered" />
  {/if} -->

  {#if PRODUCTS_SHOWED}
    {#if PRODUCTS_SHOWED.length}
      <ProductList
        {PRODUCTS_SHOWED}
        {ONLINE}
        bind:QUERY
        bind:showProductOnStock />
    {:else}
      <div class="noProducts">
        <h2>No hay productos con esa descripci√≥n :(</h2>
      </div>
    {/if}
    <Aside {PRODUCTS_TYPES} {BRANDS} bind:QUERY bind:isOpenMenu />
    <Footer
      bind:QUERY
      bind:LAST_SEARCH
      bind:showProductOnStock
      bind:isOpenMenu />
  {/if}
</main>
